body {
  margin: 0;
}
a {
  text-decoration: none;
  color: #00BCD4;
}
ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}
.tang-header-drawer {
  z-index: 1 !important;
  padding-top: 64px;
  overflow: hidden !important;
}
.tang-sub-item {
  padding-left: 60px !important;
}
.tang-reast-a {
  display: block;
  text-decoration: none;
}
#AddModule .tang-addmodule-form > div {
  margin: 30px auto;
}
#AddModule .tang-addmodule-form > div span {
  text-align: right;
  display: inline-block;
  width: 100px;
}
#AddModule .tang-addmodule-btn {
  float: right;
}
#AddModule .tang-addmodule-btn > div {
  margin-right: 20px !important;
  margin-top: 20px !important;
}
#ManageModule .tang-addmodule-btn {
  float: right;
}
#ManageModule .tang-addmodule-btn > div {
  margin-right: 20px !important;
  margin-top: 20px !important;
}
#ManageHtml .HistoryArticle table > thead > tr > th:first-child {
  display: none;
}
#Pagination {
  margin-top: 40px;
  overflow: hidden;
  text-align: center;
}
#Pagination ul {
  overflow: hidden;
  width: 380px;
  margin: 0 auto;
}
#Pagination ul li {
  cursor: pointer;
  margin: 5px;
  text-align: center;
  float: left;
  width: 30px;
  height: 30px;
  line-height: 30px;
  border-radius: 10px;
  background-color: #00BCD4;
  color: #fff;
}
#Pagination ul li.activePage {
  background-color: #FF4081;
  color: #fff;
}
#Pagination ul li.hiddenDou {
  display: none;
}



